.work {
	min-height: 100vh;
	margin: 2rem 0;
	font-family: inherit;
	width: 100%;
}
.featured-container {
	margin: 5rem 0;
}
.featured-wrapper {
	overflow: hidden;
	max-width: 90rem;
	margin: 0 auto;
}
.block-1 {
	display: flex;
	height: 500px;
}
.block-1 > div {
	padding: 1rem;
}

.block-2 {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 170px;
}
.block-2 > div {
	padding: 0rem;
}
.block-2 > div {
	padding: 0rem;
	margin: 0rem 3.5rem;
}
.block-2 > div:nth-child(even) {
	padding-right: 5rem;
}

.project-heading {
	margin: 2rem 0;
	font-size: 3rem;
	font-weight: 300;
	text-align: center;
	letter-spacing: 3px;
	background-image: linear-gradient(to right top, #52ffb3, #539987);
	background-clip: text;
	background-size: 400%;
	color: transparent;
	animation: animate 5s infinite alternate;
}
@keyframes animate {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 50% 100%;
	}
	100% {
		background-position: 100% 0%;
	}
}

.project-title {
	margin: 2rem 0;
	font-size: 3rem;
	font-weight: 300;
	text-align: center;
	letter-spacing: 3px;
	background-image: linear-gradient(to right top, #52ffb3, #539987);
	background-clip: text;
	background-size: 400%;
	color: transparent;
	animation: animate 5s infinite alternate;
}
@keyframes animate {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 50% 100%;
	}
	100% {
		background-position: 100% 0%;
	}
}

@media screen and (max-width: 767px) {
	.featured-container {
		margin: 1rem;
	}
	.featured-wrapper {
		margin: 0;
		padding: 0;
	}

	.project-heading {
		margin: 2rem 0;
		padding-top: 1rem;
		font-size: 1.8rem;
	}
	.block-1 {
		margin-top: 0rem;
		padding: 0.5rem 0;
		display: flex;
		justify-content: center;
		width: 100%;
		height: 200px;
	}
	.block-1 > div:nth-child(1) {
		padding-left: 1rem;
		padding-right: 1rem;
	}
	.block-1 > div:nth-child(2) {
		padding-left: 0.1rem;
		padding-right: 1rem;
	}
	.block-1 > div:nth-child(3) {
		padding-left: 0.1rem;
		padding-right: 1rem;
	}
	.block-1 > div {
		margin: 0;
		padding: 0.2rem 0;
	}
	.project-title {
		margin: 4rem 0;
		font-size: 1.8rem;
	}
	.block-2 {
		display: flex;
		justify-content: center;
		margin-top: 0.2rem;
		flex-direction: row;
		height: 80px;
		padding: 0;
	}
	.block-2 > div {
		padding: 0rem;
		margin: 0rem 3rem;
	}
	.block-2 > div:nth-child(even) {
		padding-right: 4rem;
	}
}

